import
uniCalendar
from "../../../../components/uni-calendar/uni-calendar"
export default {
	components: {
		uniCalendar
	},
	data() {
		return {
			taskCalendarList: [],
			dayOfMissionList: [],
			year: '',
			month: '',
			day: '',
		}
	},
	methods: {
		// 日历
		change(e) {
			// console.log(e)
			this.year = e.year,
				this.month = e.month,
				this.day = e.date,
				this.taskCalendar()
			this.getDayOfMissionList()
		},
		// 获取任务日历数据
		taskCalendar() {
			this.$minApi.taskCalendar({
				year: this.year,
				month: this.month,
			}).then(data => {
				this.taskCalendarList = data.data
				// console.log(res)
			})

		},
		// 单日任务详情列表
		getDayOfMissionList() {
			this.$minApi.dayOfMissionList({
				month: this.month,
				year: this.year,
				day: this.day,
			}, ).then(data => {
				this.dayOfMissionList = data.data
				// console.log(res)
			}).catch(err => {
				// console.log(err)
			})
		},
		// 页面跳转
		linkTo(taskId) {
			uni.navigateTo({
				url: '../details/operationalDetails?taskId=' + taskId
			})
		}
	},
	created() {

	}

};
